import { Component } from '@angular/core';
import { WeatherService } from './weather.service';
import { Chart } from 'chart.js';
import 'chart.piecelabel.js';

declare var $:any;

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.css']
})
export class AppComponent {

  chart = [];
  
  constructor(private _weather: WeatherService) {}

  ngOnInit() {
  
    this._weather.dailyForecast()
      .subscribe(res => {
        
        var pie_labels = [];
        var pie_data = [];
        var pie_backgroundColor = [];
        var pie_hoverBackgroundColor = [];
        var pie_component_data = [];
        
        for (var key in res) {
            pie_labels.push(res[key].name);
            pie_data.push(1);
            var componentData = res[key].componentData;
            var pie_monitorStatus = [];
            for (var ckey in componentData) {
                pie_monitorStatus.push(componentData[ckey].monitorStatus);
            }
            
            if(pie_monitorStatus.indexOf("BAD") != -1) {  
                pie_backgroundColor.push("#d31e1e");
                pie_hoverBackgroundColor.push("#e05959");
            } else if (pie_monitorStatus.indexOf("NORMAL") != -1){
                pie_backgroundColor.push("#ec7a07");
                pie_hoverBackgroundColor.push("#b77635");
            } else if (pie_monitorStatus.indexOf("GOOD") != -1){
                pie_backgroundColor.push("#3f9c35");
                pie_hoverBackgroundColor.push("#69a063");
            }
        }

	var linechart_data = function () {
            var tmp = null;
            $.ajax({
                'async': false,
                'type': "POST",
                'global': false,
                'dataType': 'html',
                'url': "http://rishadkv.com/linechart.php",
                'data': {},
                'success': function (data) {
                    tmp = data;
                }
            });
            return tmp;
        }();
	
	linechart_data = JSON.parse(linechart_data);
        var labelarray = [];
	var datasets = [];
	
	for(var line_key in linechart_data){
		var string = linechart_data[line_key].componentData.name;
            var status = linechart_data[line_key].componentData.monitorStatus;
            var substring = "NEXUS";
            var substring2 = "Nexus";
			var substring3 = "nexus";
	    
	    if ((string.indexOf(substring) !== -1) || (string.indexOf(substring2) !== -1) || (string.indexOf(substring3) !== -1)) {
                var monthdata = linechart_data[line_key].nexusMonitoringVrfAvgDtoLst;
                var ind_values = [];
                monthdata.forEach(function(element) {
                    ind_values.push(element.vrfAvgCount);
                    labelarray.indexOf(element.month) == -1 ? labelarray.push(element.month) : '';
                });

                if (status == 'NORMAL') {
                    var borderDash = [15,15];
                }else if(status == 'BAD'){
                    var borderDash = [3,3];
                }else{
                    var borderDash = [0,0];
                }

                var color_temp = getRandomColor();
                var unit = {
                    label: linechart_data[line_key].componentData.name,
                    data: ind_values,
                    backgroundColor: color_temp,
                    borderColor: color_temp,
                    borderDash: borderDash,
                    fill: false,
                }

                datasets.push(unit);
            }
        }
	
		function getRandomColor() {
			var letters = '0123456789ABCDEF';
			var color = '#';
			for (var i = 0; i < 6; i++) {
				color += letters[Math.floor(Math.random() * 16)];
			}
			return color;
        }

        var months = ['Jan', 'Feb', 'Mar', 'Apr', 'May', 'Jun', 'Jul', 'Aug', 'Sep', 'Oct', 'Nov', 'Dec'];

        function monthNumToName(monthnum) {
            return months[monthnum - 1] || '';
        }
        for(var key in labelarray){
            labelarray[key] = monthNumToName(labelarray[key]);
        }

        var myChart = this.chart = new Chart('canvas', {
          type: 'line',
          data: {
            labels: labelarray,
            datasets: datasets
          },
          options: {
            scales: {
                    xAxes: [{
                        display: true,
                        scaleLabel: {
                            display: true,
                            labelString: 'Month'
                        }
                    }],
                    yAxes: [{
                        ticks: {
                            beginAtZero: true
                        },
                        display: true,
                        scaleLabel: {
                            display: true,
                            labelString: 'Data Usage'
                        }
                    }]
                },
            legend: {
                    display: false,
                    position: 'bottom',
                    labels: {
                        boxWidth : 13,
                    }
                },
            tooltips: {
                callbacks: {
                   title: function(tooltipItem, data) {
                      return;
                    }
                }
            }
          }
        });
        
        this.chart = new Chart('canvas1', {
            type: 'pie',
            data: {
                labels: pie_labels,
                datasets: [{
                    data: pie_data,
                    backgroundColor: pie_backgroundColor,
                    hoverBackgroundColor: pie_hoverBackgroundColor,
                    borderColor: [
                    ],
                    borderWidth: 1
                }]
            },
            options: {
                responsive: true,
                legend: {
                        display: false
                },
                pieceLabel: {
                    render: 'label',
                    fontSize: 14,
                    fontStyle: 'bold',
                    fontColor: '#000',
                    fontFamily: '"Lucida Console", Monaco, monospace',
                },
                tooltips: {
                    enabled: false,
                    custom: function(tooltip){
                    
                        var tooltipEl = document.getElementById('chartjs-tooltip');
                        
                        if (tooltip.opacity === 0) {
                                tooltipEl.style.opacity = '0';
                                return;
                        }

		
                        tooltipEl.classList.remove('above', 'below', 'no-transform');
                        if (tooltip.yAlign) {
                                tooltipEl.classList.add(tooltip.yAlign);
                        } else {
                                tooltipEl.classList.add('no-transform');
                        }

                        function getBody(bodyItem) {
                                return bodyItem.lines;
                        }
		
                        if (tooltip.body) {
                                var titleLines = tooltip.title || [];
                                var bodyLines = tooltip.body.map(getBody);

                                var innerHtml = '<div class=""><div class="">';

                                titleLines.forEach(function(title) {
                                        innerHtml += '<div><span>' + title + '</span></div>';
                                });
                                innerHtml += '</div><div class="">';

                                bodyLines.forEach(function(body, i) {
                                    
                                    var dc = body[0].slice(0,body[0].indexOf(':'));
                                        
                                    for (var key in res) {
                                        if(res[key].name == dc) {
                                            var componentData = res[key].componentData;
                                            var country = res[key].country;
                                        }
                                    }
                                    
                                    var component_div = '<div style="background-color: #f8eee0;border-bottom-left-radius: 6px;border-bottom-right-radius: 6px;">';
                                    var pie_component_monitorStatus_array = [];
                                    for (var key in componentData) {
                                        var pie_component_name = componentData[key].name;
                                        var pie_component_monitorStatus = componentData[key].monitorStatus;
                                        pie_component_monitorStatus_array.push(pie_component_monitorStatus);
                                    
                                        if(pie_component_monitorStatus == 'BAD') {  
                                            var component_style = "text-align: center;color: #d31e1e;";
                                        } else if (pie_component_monitorStatus == 'NORMAL'){
                                            var component_style = "text-align: center;color:#ec7a07;";
                                        } else if (pie_component_monitorStatus == 'GOOD'){
                                            var component_style = "text-align: center;color:#3f9c35;";
                                        }
                                    
                                        component_div += '<div class="col-sm-12" style="' + component_style + '"><span>' + pie_component_name + '</span></div>';
                                    
                                    }
                                    component_div += '</div>';
                                    
                                    if(pie_component_monitorStatus_array.indexOf("BAD") != -1) {  
                                        var tooltip_outer_border = '#d31e1e';
                                        var tooltip_header = '#d31e1e';
                                    } else if (pie_component_monitorStatus_array.indexOf("NORMAL") != -1){
                                        var tooltip_outer_border = '#ec7a07';
                                        var tooltip_header = '#ec7a07';
                                    } else if (pie_component_monitorStatus_array.indexOf("GOOD") != -1){
                                        var tooltip_outer_border = '#3f9c35';
                                        var tooltip_header = '#3f9c35';
                                    }
                                    
                                    var colors = tooltip.labelColors[i];
                                    var style = 'background:' + colors.backgroundColor;
                                    style += '; border-color:' + colors.borderColor;
                                    style += '; border-width: 2px'; 
                                    $('#chartjs-tooltip').css('border', '3px solid');
                                    
                                    var span = '<div style="text-align: center;text-transform: uppercase;font-size: 18px;color: white;background-color: ' + tooltip_header + '">' + country + '</div>' + component_div + '</div>';
                                    $('#chartjs-tooltip').css('border-color', tooltip_outer_border);
                                            
                                    innerHtml += span;
                                });
                                innerHtml += '</div></div>';
                                
                                $('#chartjs-tooltip').css('width', '32%');

                                var tableRoot = tooltipEl.querySelector('div');
                                tableRoot.innerHTML = innerHtml;
                        }

                        var positionY = this._chart.canvas.offsetTop;
                        var positionX = this._chart.canvas.offsetLeft;

                        tooltipEl.style.opacity = '1';
                        tooltipEl.style.left = positionX + tooltip.caretX + 'px';
                        tooltipEl.style.top = positionY + tooltip.caretY + 'px';
                        tooltipEl.style.fontSize = tooltip.fontSize;
                        tooltipEl.style.fontStyle = tooltip._fontStyle;
                    }
                },
            }
        })
        
        document.getElementById('chartjsLegend').innerHTML = myChart.generateLegend();
        $("<style>.chartjsLegend ul {list-style: none}</style>").appendTo("head");
        $("<style>.chartjsLegend li span {display: inline-block;width: 12px;height: 12px;margin-right: 5px;border-radius: 25px;}</style>").appendTo("head");

        $('#lineChartFilter').on('change', function(e){
            var filter_value = $('#lineChartFilter').val();
            var linechart_data = function () {
                var tmp = null;
                $.ajax({
                    'async': false,
                    'type': "POST",
                    'global': false,
                    'dataType': 'html',
                    'url': "http://rishadkv.com/linechart.php",
                    'data': {},
                    'success': function (data) {
                        tmp = data;
                    }
                });
                return tmp;
            }();
            
            linechart_data = JSON.parse(linechart_data);
            var labelarray = [];
            var datasets = [];
	
            for(var line_key in linechart_data){
                var string = linechart_data[line_key].componentData.name;
                var status = linechart_data[line_key].componentData.monitorStatus;
                var substring = "NEXUS";
                var substring2 = "Nexus";
                            var substring3 = "nexus";

                if ((string.indexOf(substring) !== -1) || (string.indexOf(substring2) !== -1) || (string.indexOf(substring3) !== -1)) {
                    var monthdata = linechart_data[line_key].nexusMonitoringVrfAvgDtoLst;
                    var ind_values = [];
                    monthdata.forEach(function(element) {
                        ind_values.push(element.vrfAvgCount);
                        labelarray.indexOf(element.month) == -1 ? labelarray.push(element.month) : '';
                    });

                    if (status == 'NORMAL') {
                        var borderDash = [15,15];
                    }else if(status == 'BAD'){
                        var borderDash = [3,3];
                    }else{
                        var borderDash = [0,0];
                    }

                    var color_temp = getRandomColor();
                    var unit = {
                        label: linechart_data[line_key].componentData.name,
                        data: ind_values,
                        backgroundColor: color_temp,
                        borderColor: color_temp,
                        borderDash: borderDash,
                        fill: false,
                    }

                    datasets.push(unit);
                }
            }
	
            function getRandomColor() {
                    var letters = '0123456789ABCDEF';
                    var color = '#';
                    for (var i = 0; i < 6; i++) {
                            color += letters[Math.floor(Math.random() * 16)];
                    }
                    return color;
            }

            var months = ['Jan', 'Feb', 'Mar', 'Apr', 'May', 'Jun', 'Jul', 'Aug', 'Sep', 'Oct', 'Nov', 'Dec'];

            function monthNumToName(monthnum) {
                return months[monthnum - 1] || '';
            }
            for(var key in labelarray){
                labelarray[key] = monthNumToName(labelarray[key]);
            }

            var myChart = this.chart = new Chart('canvas', {
              type: 'line',
              data: {
                labels: labelarray,
                datasets: datasets
              },
              options: {
                scales: {
                        xAxes: [{
                            display: true,
                            scaleLabel: {
                                display: true,
                                labelString: 'Month'
                            }
                        }],
                        yAxes: [{
                            ticks: {
                                beginAtZero: true
                            },
                            display: true,
                            scaleLabel: {
                                display: true,
                                labelString: 'Data Usage'
                            }
                        }]
                    },
                legend: {
                        display: false,
                        position: 'bottom',
                        labels: {
                            boxWidth : 13,
                        }
                    },
                tooltips: {
                    callbacks: {
                       title: function(tooltipItem, data) {
                          return;
                        }
                    }
                }
              }
            });
            
            document.getElementById('chartjsLegend').innerHTML = myChart.generateLegend();
            $("<style>.chartjsLegend ul {list-style: none}</style>").appendTo("head");
            $("<style>.chartjsLegend li span {display: inline-block;width: 12px;height: 12px;margin-right: 5px;border-radius: 25px;}</style>").appendTo("head");

        });

    })
  }
}


